<template>
  <section
    fluid
    class="some-d3js">
    <h1>Some d3.js</h1>
    <p>
      A demo of a bubble chart, it uses d3.js for rendering.
    </p>
    <label>Strength
      <input
        v-model="strength"
        type="number"
        @change="initSimulation" >
    </label>
    <label>Min Size
      <input
        v-model="minSize"
        type="number"
        @change="initSimulation" >
    </label>
    <label>maxSize
      <input
        v-model="maxSize"
        type="number"
        @change="initSimulation" >
    </label>
    <button
      type="button"
      @click="add">Add</button>
    <div id="bubble-chart"/>
  </section>
</template>
<script>
import * as d3 from 'd3'

export default {
  name: 'SomeD3js',
  components: {},
  data () {
    return {
      width: 600,
      height: 400,
      strength: -100,
      minSize: 10,
      maxSize: 30,
      data: [
        {
          id: Math.random(),
          title: 'Type of thing1',
          category: 'Foo',
          count: 48
        },
        {
          id: Math.random(),
          title: 'Type of thing2',
          category: 'Foo',
          count: 1922
        },
        {
          id: Math.random(),
          title: 'Type of thing3',
          category: 'Foozy',
          count: 999
        },
        {
          id: Math.random(),
          title: 'Another',
          category: 'Bar',
          count: 1700
        },
        {
          id: Math.random(),
          title: 'Something else',
          category: 'Bar',
          count: 1000
        },
        {
          id: Math.random(),
          title: 'Something else is here',
          category: 'Bar',
          count: 2200
        }
      ]
    }
  },
  mounted () {
    this.initBubbleChart()
  },
  methods: {
    add () {
      this.data.push({
        id: Math.random(),
        title: 'More things',
        category: 'More',
        count: 500,
        x: Math.random() * 10000,
        y: Math.random() * 10000
      })

      this.restart()
    },
    initSimulation () {
      const scaleRadius = d3
        .scaleLinear()
        .domain([
          d3.min(this.data, d => d.count),
          d3.max(this.data, d => d.count)
        ])
        .range([this.minSize, this.maxSize])

      if (this.simulation) {
        this.simulation.nodes(this.data)
        this.simulation.alpha(1).restart()
      } else {
        this.simulation = d3.forceSimulation(this.data)
      }

      this.simulation
        .force('charge', d3.forceManyBody().strength(this.strength))
        .force('x', d3.forceX())
        .force('y', d3.forceY())
        .on('tick', e => {
          this.node
            .attr('r', d => scaleRadius(d.count))
            .attr('cx', d => d.x)
            .attr('cy', d => d.y)
        })
    },
    initBubbleChart () {
      const chart = d3.select('#bubble-chart')

      this.tooltip = chart
        .append('div')
        .attr('class', 'tooltip')
        .text('')

      this.colorCircles = d3.scaleOrdinal(d3.schemeCategory10)

      this.transformToCenter = 'translate(' + [this.width / 2, this.height / 2] + ')'

      this.svg = chart
        .append('svg')
        .attr('width', this.width)
        .attr('height', this.height)

      this.node = this.svg
        .selectAll('circle')

      this.restart()
    },
    restart () {
      this.node = this.node.data(this.data, d => d.title)
      this.node.exit().remove()

      this.node = this.node.enter()
        .append('circle')
        .attr('transform', this.transformToCenter)
        .style('fill', d => this.colorCircles(d.category))
        .on('mouseover', d => this.tooltip
          .html(this.getTooltipText(d))
          .style('opacity', 0.9)
          .style('z-index', 100))
        .on('mousemove', () => this.tooltip
          .style('top', d3.event.pageY - 10 + 'px')
          .style('left', d3.event.pageX + 10 + 'px'))
        .on('mouseout', () => this.tooltip
          .style('opacity', 0)
          .style('z-index', -1))
        .merge(this.node)

      this.initSimulation()
    },
    getTooltipText (d) {
      return '' +
        '<div>' + d.title + '</div>' +
        '<div><strong>' + d.count + '</strong></div>' +
        '<div><em>' + d.category + '</em></div>'
    }
  }
}
</script>

<style lang="scss">
.some-d3js {
  #bubble-chart {
    background-color: cornsilk;
  }

  .tooltip {
    position: absolute;
    color: white;
    padding: .3em;
    background-color: #999;
    border-radius: .5em;
    transition: opacity .4s;
    top: -9999;
    left: 0;
    opacity: 0;
    z-index: -1;
  }
}
</style>



